import React from 'react';
import dayjs from 'dayjs';
import ColorPicker from 'material-ui-color-picker';
import { DateTimePicker, MuiPickersUtilsProvider } from '@material-ui/pickers';
import DayjsUtils from '@date-io/dayjs';

import Typography from '@material-ui/core/Typography';
import TextField from '@material-ui/core/TextField';
import MenuItem from '@material-ui/core/MenuItem';
import { makeStyles } from '@material-ui/styles';
import Paper from '@material-ui/core/Paper';
import Grid from '@material-ui/core/Grid';

const useStyles = makeStyles(theme => ({
  root: {
    margin: theme.spacing(2),
    padding: theme.spacing(2),
    backgroundColor: '#e9f1f9',
  },
  titleMargin: {
    marginBottom: theme.spacing(1),
  },
}));

function BookingInfo({ errors, appointment, handleChange, categories, workers }) {
  const classes = useStyles();

  return (
    <Paper className={classes.root}>
      <Typography variant='h6' className={classes.titleMargin}>
        Booking information
      </Typography>
      <Grid container spacing={2}>
        <Grid item xs={12} md={3}>
          <MuiPickersUtilsProvider utils={DayjsUtils}>
            <DateTimePicker
              label='Datum'
              value={dayjs(appointment.startDate).format('DD.MM.YYYY HH:mm')}
              format='DD/MM/YYYY HH:mm'
              onChange={date => handleChange('startDate', date.format('DD.MM.YYYY HH:mm'))}
            />
          </MuiPickersUtilsProvider>
        </Grid>
        <Grid item xs={12} md={3}>
        </Grid>
        <Grid item xs={12} md={3}>
          <TextField
            error={errors.worker}
            helperText={errors.worker && 'Field required'}
            select
            fullWidth
            label="Raum"
            value={appointment.worker}
            onChange={e => handleChange('worker', e.target.value)}
          >
            {workers.map(option => (
              <MenuItem key={option._id} value={option._id}>
                {option.name}
              </MenuItem>
            ))}
          </TextField>
        </Grid>
        <Grid item xs={12} md={3}>
          <ColorPicker
            name='color'
            label='Color'
            error={errors.color}
            helperText={errors.color && 'Field required'}
            defaultValue={appointment.color}
            value={appointment.color}
            TextFieldProps={{
              value: appointment.color
            }}
            onChange={value => handleChange('color', value)}
          />
        </Grid>
      </Grid>
    </Paper>
  )
}

export default React.memo(BookingInfo);
