import React, {useCallback, useEffect, useState} from 'react';
import dayjs from 'dayjs';
import Scheduler, { Resource, View, AppointmentDragging, Editing } from 'devextreme-react/scheduler';

import { makeStyles } from '@material-ui/core/styles';
import CardContent from '@material-ui/core/CardContent';
import CardHeader from '@material-ui/core/CardHeader';
import Typography from '@material-ui/core/Typography';
import Card from '@material-ui/core/Card';

import { useStore } from '../../store/DataProvider';

import './style.scss';

import CircularProgress from '@material-ui/core/CircularProgress';

const useStyles = makeStyles(() => ({
  root: {
    maxWidth: 345,
  },
}));

const currentDate = new Date();

const views = [
  {
    type: 'day',
    name: 'Day',
  },
  {
    type: 'week',
    name: 'Week',
  },
  {
    type: 'month',
    name: 'Month',
  },
  {
    type: 'timelineDay',
    name: 'Day timeline'
  },
  {
    type: 'timelineWorkWeek',
    name: 'Week timeline'
  },
  {
    type: 'timelineMonth',
    name: 'Month timeline'
  }
];

const groups = ['worker'];

export default function Calendars({ history }) {
  const classes = useStyles();

  const { state: { workers, prices, appointments, clients, categories }, actions: { setSelectedAppointment, updateAppointment } } = useStore();

  const [loading, setLoading] = useState(false);
  const [data, setData] = useState(appointments.map(item => ({
    ...item,
    clientId: { ...item.clientId },
    worker: item.worker || 0,
    text: `${item.clientId.name} ${item.clientId.lastName} - ${item.clientId.phone} - ${item.clientId.email}`,
    startDate: item.time,
    endDate: item.timeEnd,
  })));

  const handleDragEnd = useCallback(({ newData, ...event }) => {
    setLoading(true);

    const appointment = {
      ...newData,
      time: newData.startDate,
      timeEnd: newData.endDate,
      worker: newData.worker === 0 ? null : newData.worker,
      duration: dayjs(newData.endDate).diff(newData.startDate, 'minute'),
    };

    const newAppointmentIndex = data.findIndex(item => item._id === newData._id);
    const dataList = [ ...data ];
    dataList[newAppointmentIndex] = { ...appointment };

    setData(dataList);

    return updateAppointment(appointment).then(() => setLoading(false));
  }, [updateAppointment, data]);

  const workerData = workers.map((item, index) => ({ id: item._id, text: item.name }));
  workerData.push({ id: 0, text: 'Not matter' });

  useEffect(() => {
    setData(appointments.map(item => ({
      ...item,
      clientId: { ...item.clientId },
      worker: item.worker || 0,
      text: `${item.clientId.name} ${item.clientId.lastName} - ${item.clientId.phone} - ${item.clientId.email}`,
      startDate: item.time,
      endDate: item.timeEnd,
    })));
  }, [appointments]);

  return (
    <div id="scheduler">
      {loading && <div className='start-loading'>
        <div className={classes.busyRoot}>
          <CircularProgress color="secondary" />
        </div>
      </div>}
      <Scheduler
        shadeUntilCurrentTime
        showCurrentTimeIndicator
        dataSource={data}
        views={views}
        groups={groups}
        groupByDate={true}
        showAllDayPanel={false}
        defaultCurrentView="week"
        defaultCurrentDate={currentDate}
        startDayHour={8}
        endDayHour={24}
        cellDuration={15}
        firstDayOfWeek={1}
        timeCellTemplate={data => {
          const newtext = data.text.replace('PM', '');
          const text = newtext.replace('AM', '');

          return text === '' ? text : dayjs(data.date).format('HH:mm');
        }}
        onAppointmentUpdating={handleDragEnd}
        onAppointmentFormOpening={data => {
          setSelectedAppointment(data.appointmentData).then(() => {
            history.push(`/appointments/0`);
          });
          data.cancel = true;
          return data;
        }}
        onAppointmentDblClick={(data) => {
          setSelectedAppointment(data.appointmentData).then(() => {
            history.push(`/appointments/${data.appointmentData._id}`);
          });
          data.cancel = true;
          return data;
        }}
        appointmentTooltipComponent={({ data: { targetedAppointmentData: item }}) => {
          let client = item.clientId;
          client = item.clientId.name ? item.clientId : clients.find(client => client._id === Object.values(item.clientId).join(''));
          const { name, email, phone, lastName } = client;
          return (
            <Card className={classes.root}>
              <CardHeader
                title={`${name} ${lastName}`}
                subheader={`${dayjs(item.time).format('MMMM DD HH:mm')} - ${dayjs(item.timeEnd).format('HH:mm')}`}
              />
              <CardContent>
                <Typography variant="body1" color="textSecondary" component="p" style={{ whiteSpace: 'normal' }}>
                  Services: {prices.filter(price => item.serviceIds.includes(price._id)).map(price => price.title).join(', ')}
                </Typography>
                <Typography variant="body2" color="textSecondary" component="p">
                  Email: {email}
                </Typography>
                <Typography variant="body2" color="textSecondary" component="p">
                  Phone: {phone}
                </Typography>
              </CardContent>
            </Card>
          )
        }}
        appointmentComponent={({ data: { targetedAppointmentData: item }}) => {
          let client = item.clientId;
          client = item.clientId.name ? item.clientId : clients.find(client => client._id === Object.values(item.clientId).join(''));
          const price = prices.find(price => price._id === item.serviceIds[0]);
          const cat = categories.find(cat => cat._id === price.categoryId);
          
          const { name, email, phone, lastName } = client;
          return (
            <div style={{
              backgroundColor: dayjs().isSame(item.time, 'day') ? 'red' : cat ? cat.color : '#5c6bc0',
              width: '100%',
              height: '100%',
              padding: '5px 7px'
            }}>
              <p>{name} {lastName}</p>
              <p>Email: {email}</p>
              <p>Phone: {phone}</p>
              <p>{dayjs(item.time).format('HH:mm')} - {dayjs(item.timeEnd).format('HH:mm')}</p>
            </div>
          );
        }}
        crossScrollingEnabled={true}>
        <Resource
          fieldExpr="worker"
          allowMultiple={false}
          dataSource={workerData}
          useColorAsDefault
          label="Worker"
        />
        <View
          type="week"
          name="Week"
        />
        <View
          type="month"
          name="Month"
        />
        <View
          type="timelineWeek"
          name="Timeline Week"
          groupOrientation="horizontal"
        />
        <AppointmentDragging

        />
      </Scheduler>
    </div>
  );
}
