import React from 'react';
import MaterialTable from 'material-table';

import ExpansionPanelDetails from '@material-ui/core/ExpansionPanelDetails';
import ExpansionPanelSummary from '@material-ui/core/ExpansionPanelSummary';
import ExpansionPanel from '@material-ui/core/ExpansionPanel';
import Typography from '@material-ui/core/Typography';
import TableBody from '@material-ui/core/TableBody';
import TableCell from '@material-ui/core/TableCell';
import TableHead from '@material-ui/core/TableHead';
import TableRow from '@material-ui/core/TableRow';
import Checkbox from '@material-ui/core/Checkbox';
import { makeStyles } from '@material-ui/styles';
import Table from '@material-ui/core/Table';
import Paper from '@material-ui/core/Paper';

import ExpandMoreIcon from '@material-ui/icons/ExpandMore';

const useStyles = makeStyles(theme => ({
  root: {
    margin: theme.spacing(2),
    padding: theme.spacing(2),
    backgroundColor: '#e9f1f9',
  },
  titleMargin: {
    marginBottom: theme.spacing(1),
  },
  table: {
    minWidth: 650,
  },
}));

function ServiceList({ errors, subCategories, appointment, categories, prices, handleServiceAdd }) {
  const classes = useStyles();
  const columns = [
    {
      field: 'title',
      title: 'Name',
      render: (rowData) => (
        <>
          <Checkbox
            checked={appointment.serviceIds.includes(rowData._id)}
            onChange={(e, checked) => handleServiceAdd(rowData, checked)}
          />
          {rowData.title}
        </>
      )
    },
    {
      field: 'subCategoryId',
      grouping: true,
      defaultGroupOrder: 0,
      title: 'Group',
      render: id => subCategories.find(item => item._id === id)?.title,
    },
    {
      field: 'sale',
      title: 'In sale',
      render: rowData => `Sale: ${rowData.sale ? 'Yes' : 'No'}`,
    },
    {
      field: 'duration',
      title: 'Duration',
      render: rowData => `${Math.floor(Number(rowData.duration) / 60)} H ${Number(rowData.duration) % 60} m`,
    },
    {
      field: 'price',
      title: 'Price',
      render: rowData => `€ ${rowData.price}`,
    }
  ];
  return (
    <Paper className={classes.root}>
      <Typography variant='h6' className={classes.titleMargin}>
        Service lists {errors.serviceIds && <Typography variant='h5' color='error'>Minimum 1 service must be selected</Typography>}
      </Typography>
      {categories.map(category => {
        const subs = prices.filter(price => price.categoryId === category._id).reduce((acc, elem) => {
          if(acc[elem.subCategoryId]) {
            acc[elem.subCategoryId].data.push(elem);
          } else if (elem.subCategoryId !== null) {
            acc[elem.subCategoryId] = { data: [] };
          }
          return acc;
        }, {});
        if (!Object.keys(subs).length) {
          return (
            <ExpansionPanel className='category-panel' key={category._id}>
              <ExpansionPanelSummary
                expandIcon={<ExpandMoreIcon />}
                aria-controls="panel1a-content"
                id="panel1a-header"
              >
                <Typography>{category.title}</Typography>
              </ExpansionPanelSummary>
              <ExpansionPanelDetails>
                <Table className={classes.table} size="small" aria-label="a dense table">
                  <TableHead>
                    <TableRow>
                      <TableCell>Name</TableCell>
                      <TableCell align="left">In Sale </TableCell>
                      <TableCell align="left">Duration</TableCell>
                      <TableCell align="left">Price</TableCell>
                    </TableRow>
                  </TableHead>
                  <TableBody>
                    {prices.filter(price => price.categoryId === category._id).map(price => (
                      <TableRow
                        style={{ cursor: 'pointer' }}
                        key={price._id}
                        onClick={() => handleServiceAdd(price, !appointment.serviceIds.includes(price._id))}
                      >
                        <TableCell component="th" scope="row">
                          <Checkbox
                            checked={appointment.serviceIds.includes(price._id)}
                            onChange={(e, checked) => handleServiceAdd(price, checked)}
                          />
                          {price.title}
                        </TableCell>
                        <TableCell align="left">Sale: {price.sale ? 'Yes' : 'No'}</TableCell>
                        <TableCell align="left">{`${Math.floor(Number(price.duration) / 60)} H ${Number(price.duration) % 60} m`}</TableCell>
                        <TableCell align="left">€ {price.price}</TableCell>
                      </TableRow>
                    ))}
                  </TableBody>
                </Table>
              </ExpansionPanelDetails>
            </ExpansionPanel>
          )
        } else {
          return (
            <ExpansionPanel className='category-panel' key={category._id}>
              <ExpansionPanelSummary
                expandIcon={<ExpandMoreIcon />}
                aria-controls="panel1a-content"
                id="panel1a-header"
              >
                <Typography>{category.title}</Typography>
              </ExpansionPanelSummary>
              <ExpansionPanelDetails>
                <MaterialTable
                  className={classes.table}
                  style={{ backgroundColor: 'transparent', width: '100%', boxShadow: 'none' }}
                  columns={columns}
                  icons={{
                    DetailPanel: ExpandMoreIcon,
                  }}
                  options={{
                    grouping: false,
                    header: false,
                    paging: false,
                    search: false,
                    sorting: false,
                    toolbar: false,
                  }}
                  onRowClick={(e, price) => handleServiceAdd(price, !appointment.serviceIds.includes(price._id))}
                  data={[...prices].filter(price => price.categoryId === category._id).map(price => ({ ...price }))}
               />
              </ExpansionPanelDetails>
            </ExpansionPanel>
          );
        }
      })}
      <Table>
        <TableHead>
          <TableRow>
            <TableCell>Total</TableCell>
            <TableCell align="right"> </TableCell>
            <TableCell align="right"> </TableCell>
          </TableRow>
        </TableHead>
        <TableBody>
          <TableRow>
            <TableCell colSpan={3} align="right">Subtotal: € {appointment.subTotal}</TableCell>
          </TableRow>
          <TableRow>
            <TableCell colSpan={3} align="right">Total: € {appointment.total}</TableCell>
          </TableRow>
        </TableBody>
      </Table>
    </Paper>
  );
}

export default React.memo(ServiceList);
